<template>
    <div>
        <div v-for="item in state.todos" :key="item.author">
            <h4>
                {{ item. author }}
            </h4>
            <h4>
                {{ item.todo }}
            </h4>
        
        </div>
    </div>
</template>
<script>
import { reactive, onMounted } from 'vue';
export default {
    setup(){
        const state= reactive({
            todos: {}
        })

        function GetAll(){
            fetch("http://localhost:3000/todos")
            .then(res=> res.json())
            .then(data=>{
                state.todos=data
                
            })
        }
        onMounted(()=>{GetAll()})
        return {state,  GetAll}   
    },
    
}
</script>
